'use client'

import { useState, useEffect } from 'react'

interface Stage {
  id: string
  name: string
  color: string
  stageType: 'open' | 'won' | 'lost'
  winProbability: number
  displayOrder: number
}

interface StageModalProps {
  isOpen: boolean
  onClose: () => void
  onSave: () => void
  stage: Stage | null
  pipelineId: string
  subAccountId: string
  existingStagesCount: number
}

const STAGE_COLORS = [
  '#3B82F6', // Blue
  '#10B981', // Green
  '#F59E0B', // Amber
  '#EF4444', // Red
  '#8B5CF6', // Purple
  '#EC4899', // Pink
  '#06B6D4', // Cyan
  '#F97316', // Orange
  '#6366F1', // Indigo
  '#14B8A6', // Teal
  '#84CC16', // Lime
  '#A855F7', // Violet
]

export default function StageModal({
  isOpen,
  onClose,
  onSave,
  stage,
  pipelineId,
  subAccountId,
  existingStagesCount,
}: StageModalProps) {
  const [formData, setFormData] = useState({
    name: '',
    color: STAGE_COLORS[0],
    stageType: 'open' as 'open' | 'won' | 'lost',
    winProbability: 50,
    displayOrder: existingStagesCount,
  })
  const [saving, setSaving] = useState(false)
  const [error, setError] = useState<string | null>(null)

  // Load form data when stage changes
  useEffect(() => {
    if (stage) {
      setFormData({
        name: stage.name || '',
        color: stage.color || STAGE_COLORS[0],
        stageType: stage.stageType || 'open',
        winProbability: stage.winProbability ?? 50,
        displayOrder: stage.displayOrder ?? existingStagesCount,
      })
    } else {
      // Reset form for new stage
      setFormData({
        name: '',
        color: STAGE_COLORS[existingStagesCount % STAGE_COLORS.length],
        stageType: 'open',
        winProbability: 50,
        displayOrder: existingStagesCount,
      })
    }
  }, [stage, existingStagesCount])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setError(null)

    if (!formData.name.trim()) {
      setError('Stage name is required')
      return
    }

    try {
      setSaving(true)

      const payload = {
        ...formData,
        pipelineId,
        subAccountId,
      }

      const url = stage
        ? `/api/v1/pipelines/${pipelineId}/stages/${stage.id}`
        : `/api/v1/pipelines/${pipelineId}/stages`
      const method = stage ? 'PUT' : 'POST'

      const response = await fetch(url, {
        method,
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload),
      })

      const result = await response.json()

      if (!result.success) {
        throw new Error(result.error)
      }

      onSave()
    } catch (err: any) {
      setError(err.message || 'Failed to save stage')
    } finally {
      setSaving(false)
    }
  }

  if (!isOpen) return null

  return (
    <div className="fixed inset-0 z-50 overflow-y-auto">
      {/* Backdrop */}
      <div
        className="fixed inset-0 bg-black bg-opacity-50 transition-opacity"
        onClick={onClose}
      />

      {/* Modal */}
      <div className="flex min-h-full items-center justify-center p-4">
        <div className="relative bg-white rounded-xl shadow-xl max-w-lg w-full">
          {/* Header */}
          <div className="border-b border-gray-200 px-6 py-4 flex items-center justify-between">
            <h2 className="text-xl font-bold text-gray-900">
              {stage ? 'Edit Stage' : 'Add New Stage'}
            </h2>
            <button
              onClick={onClose}
              className="text-gray-400 hover:text-gray-600 transition-colors"
            >
              <svg className="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>

          {/* Form */}
          <form onSubmit={handleSubmit} className="p-6 space-y-6">
            {error && (
              <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded-lg">
                {error}
              </div>
            )}

            {/* Stage Name */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Stage Name <span className="text-red-500">*</span>
              </label>
              <input
                type="text"
                value={formData.name}
                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                placeholder="e.g., Qualified Lead, Proposal Sent"
                required
              />
            </div>

            {/* Stage Type */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Stage Type
              </label>
              <div className="grid grid-cols-3 gap-3">
                {[
                  { value: 'open', label: 'Open', icon: 'üìã', description: 'In progress' },
                  { value: 'won', label: 'Won', icon: 'üéâ', description: 'Opportunity won' },
                  { value: 'lost', label: 'Lost', icon: '‚ùå', description: 'Opportunity lost' },
                ].map((type) => (
                  <button
                    key={type.value}
                    type="button"
                    onClick={() => setFormData({ 
                      ...formData, 
                      stageType: type.value as 'open' | 'won' | 'lost',
                      winProbability: type.value === 'won' ? 100 : type.value === 'lost' ? 0 : formData.winProbability
                    })}
                    className={`p-3 rounded-lg border-2 text-center transition-colors ${
                      formData.stageType === type.value
                        ? 'border-green-500 bg-green-50'
                        : 'border-gray-200 hover:border-gray-300'
                    }`}
                  >
                    <div className="text-2xl mb-1">{type.icon}</div>
                    <div className="font-medium text-gray-900">{type.label}</div>
                    <div className="text-xs text-gray-500">{type.description}</div>
                  </button>
                ))}
              </div>
            </div>

            {/* Win Probability */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Win Probability: {formData.winProbability}%
              </label>
              <input
                type="range"
                min="0"
                max="100"
                step="5"
                value={formData.winProbability}
                onChange={(e) => setFormData({ ...formData, winProbability: parseInt(e.target.value) })}
                className="w-full h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer accent-green-600"
                disabled={formData.stageType === 'won' || formData.stageType === 'lost'}
              />
              <div className="flex justify-between text-xs text-gray-400 mt-1">
                <span>0%</span>
                <span>50%</span>
                <span>100%</span>
              </div>
              {(formData.stageType === 'won' || formData.stageType === 'lost') && (
                <p className="text-xs text-gray-500 mt-1">
                  Probability is fixed at {formData.stageType === 'won' ? '100%' : '0%'} for {formData.stageType} stages
                </p>
              )}
            </div>

            {/* Color Picker */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Stage Color
              </label>
              <div className="flex flex-wrap gap-2">
                {STAGE_COLORS.map((color) => (
                  <button
                    key={color}
                    type="button"
                    onClick={() => setFormData({ ...formData, color })}
                    className={`w-8 h-8 rounded-full transition-transform ${
                      formData.color === color ? 'ring-2 ring-offset-2 ring-gray-400 scale-110' : ''
                    }`}
                    style={{ backgroundColor: color }}
                  />
                ))}
              </div>
              {/* Custom color input */}
              <div className="mt-3 flex items-center gap-2">
                <input
                  type="color"
                  value={formData.color}
                  onChange={(e) => setFormData({ ...formData, color: e.target.value })}
                  className="w-10 h-10 rounded cursor-pointer border border-gray-200"
                />
                <input
                  type="text"
                  value={formData.color}
                  onChange={(e) => setFormData({ ...formData, color: e.target.value })}
                  className="flex-1 px-3 py-2 border border-gray-200 rounded-lg text-sm font-mono"
                  placeholder="#000000"
                />
              </div>
            </div>

            {/* Display Order */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Display Order
              </label>
              <input
                type="number"
                value={formData.displayOrder}
                onChange={(e) => setFormData({ ...formData, displayOrder: parseInt(e.target.value) || 0 })}
                className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                min="0"
              />
              <p className="text-xs text-gray-500 mt-1">
                Lower numbers appear first (left to right)
              </p>
            </div>

            {/* Actions */}
            <div className="flex items-center justify-end gap-3 pt-4 border-t border-gray-200">
              <button
                type="button"
                onClick={onClose}
                className="px-4 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
              >
                Cancel
              </button>
              <button
                type="submit"
                disabled={saving}
                className="px-6 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
              >
                {saving ? 'Saving...' : stage ? 'Update Stage' : 'Create Stage'}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}
