'use client'

import { useSortable } from '@dnd-kit/sortable'
import { CSS } from '@dnd-kit/utilities'

interface Contact {
  id: string
  firstName: string
  lastName: string
  email: string
}

interface Opportunity {
  id: string
  name: string
  description?: string
  value: number
  currency: string
  status: string
  priority: string
  probability: number
  expectedCloseDate?: string
  contact?: Contact
  stageId: string
  pipelineId: string
  tags: string[]
  lastActivityAt: string
  createdAt: string
}

interface OpportunityCardProps {
  opportunity: Opportunity
  isDragging?: boolean
  onEdit?: (opportunity: Opportunity) => void
  onDelete?: (opportunityId: string) => void
}

export default function OpportunityCard({ opportunity, isDragging, onEdit, onDelete }: OpportunityCardProps) {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging: isSortableDragging,
  } = useSortable({ id: opportunity.id })
  
  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
    opacity: isSortableDragging ? 0.5 : 1,
  }
  
  const priorityColors: Record<string, string> = {
    low: 'bg-gray-100 text-gray-600',
    medium: 'bg-blue-100 text-blue-600',
    high: 'bg-orange-100 text-orange-600',
    urgent: 'bg-red-100 text-red-600',
  }
  
  const formatCurrency = (value: number, currency: string = 'USD') => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency,
      minimumFractionDigits: 0,
      maximumFractionDigits: 0,
    }).format(value)
  }
  
  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    return date.toLocaleDateString('en-US', { month: 'short', day: 'numeric' })
  }
  
  const getInitials = (contact?: Contact) => {
    if (!contact) return '?'
    return `${contact.firstName?.[0] || ''}${contact.lastName?.[0] || ''}`.toUpperCase()
  }
  
  return (
    <div
      ref={setNodeRef}
      style={style}
      {...attributes}
      {...listeners}
      className={`
        bg-white rounded-lg shadow-sm border border-gray-200 p-3 mb-2
        cursor-grab active:cursor-grabbing
        hover:shadow-md transition-shadow
        ${isDragging ? 'shadow-lg ring-2 ring-green-500 ring-opacity-50' : ''}
      `}
    >
      {/* Header - Contact Name (Opportunity Title) */}
      <div className="flex items-start justify-between mb-2">
        <div className="flex items-center gap-2 flex-1 pr-2">
          {opportunity.contact && (
            <div className="w-7 h-7 rounded-full bg-green-100 flex items-center justify-center text-xs font-semibold text-green-700">
              {getInitials(opportunity.contact)}
            </div>
          )}
          <h4 className="font-medium text-gray-900 text-sm line-clamp-2">
            {opportunity.name}
          </h4>
        </div>
        <span className={`text-xs px-2 py-0.5 rounded-full flex-shrink-0 ${priorityColors[opportunity.priority] || priorityColors.medium}`}>
          {opportunity.priority}
        </span>
      </div>
      
      {/* Contact Email */}
      {opportunity.contact?.email && (
        <div className="text-xs text-gray-500 mb-2 truncate">
          {opportunity.contact.email}
        </div>
      )}
      
      {/* Value */}
      <div className="text-lg font-bold text-green-600 mb-2">
        {formatCurrency(opportunity.value, opportunity.currency)}
      </div>
      
      {/* Tags */}
      {opportunity.tags && opportunity.tags.length > 0 && (
        <div className="flex flex-wrap gap-1 mb-2">
          {opportunity.tags.slice(0, 2).map((tag, index) => (
            <span
              key={index}
              className="text-xs px-2 py-0.5 bg-gray-100 text-gray-600 rounded"
            >
              {tag}
            </span>
          ))}
          {opportunity.tags.length > 2 && (
            <span className="text-xs text-gray-400">+{opportunity.tags.length - 2}</span>
          )}
        </div>
      )}
      
      {/* Footer */}
      <div className="flex items-center justify-between text-xs text-gray-400 pt-2 border-t border-gray-100">
        <div className="flex items-center gap-1">
          <span>üìä</span>
          <span>{opportunity.probability}%</span>
        </div>
        {opportunity.expectedCloseDate && (
          <div className="flex items-center gap-1">
            <span>üìÖ</span>
            <span>{formatDate(opportunity.expectedCloseDate)}</span>
          </div>
        )}
      </div>
      
      {/* Action buttons (shown on hover) */}
      {(onEdit || onDelete) && !isDragging && (
        <div className="flex items-center justify-end gap-1 mt-2 pt-2 border-t border-gray-100 opacity-0 group-hover:opacity-100 transition-opacity">
          {onEdit && (
            <button
              onClick={(e) => {
                e.stopPropagation()
                onEdit(opportunity)
              }}
              className="p-1 text-gray-400 hover:text-blue-600 transition-colors"
              title="Edit opportunity"
            >
              ‚úèÔ∏è
            </button>
          )}
          {onDelete && (
            <button
              onClick={(e) => {
                e.stopPropagation()
                onDelete(opportunity.id)
              }}
              className="p-1 text-gray-400 hover:text-red-600 transition-colors"
              title="Delete opportunity"
            >
              üóëÔ∏è
            </button>
          )}
        </div>
      )}
    </div>
  )
}
