'use client'

import { useState } from 'react'
import { useDroppable } from '@dnd-kit/core'
import { SortableContext, verticalListSortingStrategy } from '@dnd-kit/sortable'
import OpportunityCard from './OpportunityCard'

interface Contact {
  id: string
  firstName: string
  lastName: string
  email: string
}

interface Opportunity {
  id: string
  name: string
  description?: string
  value: number
  currency: string
  status: string
  priority: string
  probability: number
  expectedCloseDate?: string
  contact?: Contact
  stageId: string
  pipelineId: string
  tags: string[]
  lastActivityAt: string
  createdAt: string
}

interface Stage {
  id: string
  name: string
  color: string
  stageType: 'open' | 'won' | 'lost'
  winProbability: number
  displayOrder: number
  deals: Opportunity[]
}

interface StageColumnProps {
  stage: Stage
  onAddOpportunity: () => void
  onEditOpportunity: (opportunity: Opportunity) => void
  onDeleteOpportunity: (opportunityId: string) => void
  onEditStage?: (stage: Stage) => void
  onDeleteStage?: (stageId: string) => void
}

export default function StageColumn({ stage, onAddOpportunity, onEditOpportunity, onDeleteOpportunity, onEditStage, onDeleteStage }: StageColumnProps) {
  const [showMenu, setShowMenu] = useState(false)
  const { setNodeRef, isOver } = useDroppable({
    id: stage.id,
  })
  
  const totalValue = stage.deals.reduce((sum, opp) => sum + (opp.value || 0), 0)
  
  const formatCurrency = (value: number) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
      minimumFractionDigits: 0,
      maximumFractionDigits: 0,
    }).format(value)
  }
  
  const stageTypeIcons: Record<string, string> = {
    open: '',
    won: 'üéâ',
    lost: '‚ùå',
  }
  
  return (
    <div
      className={`
        w-72 flex-shrink-0 bg-gray-50 rounded-lg flex flex-col
        ${isOver ? 'ring-2 ring-green-500 ring-opacity-50 bg-green-50' : ''}
      `}
    >
      {/* Stage Header */}
      <div className="p-3 border-b border-gray-200">
        <div className="flex items-center justify-between mb-1">
          <div className="flex items-center gap-2">
            <div
              className="w-3 h-3 rounded-full"
              style={{ backgroundColor: stage.color }}
            />
            <h3 className="font-semibold text-gray-900">
              {stage.name} {stageTypeIcons[stage.stageType]}
            </h3>
          </div>
          <div className="flex items-center gap-1">
            <span className="text-sm text-gray-500 bg-gray-200 px-2 py-0.5 rounded-full">
              {stage.deals.length}
            </span>
            {/* Stage Menu */}
            {(onEditStage || onDeleteStage) && (
              <div className="relative">
                <button
                  onClick={() => setShowMenu(!showMenu)}
                  className="p-1 text-gray-400 hover:text-gray-600 hover:bg-gray-200 rounded transition-colors"
                >
                  <svg className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 5v.01M12 12v.01M12 19v.01M12 6a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2z" />
                  </svg>
                </button>
                {showMenu && (
                  <>
                    <div
                      className="fixed inset-0 z-10"
                      onClick={() => setShowMenu(false)}
                    />
                    <div className="absolute right-0 top-full mt-1 w-36 bg-white border border-gray-200 rounded-lg shadow-lg z-20">
                      {onEditStage && (
                        <button
                          onClick={() => {
                            setShowMenu(false)
                            onEditStage(stage)
                          }}
                          className="w-full px-3 py-2 text-left text-sm text-gray-700 hover:bg-gray-50 flex items-center gap-2"
                        >
                          <svg className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
                          </svg>
                          Edit Stage
                        </button>
                      )}
                      {onDeleteStage && (
                        <button
                          onClick={() => {
                            setShowMenu(false)
                            if (stage.deals.length > 0) {
                              alert('Cannot delete stage with opportunities. Move or delete opportunities first.')
                              return
                            }
                            if (confirm(`Are you sure you want to delete "${stage.name}"?`)) {
                              onDeleteStage(stage.id)
                            }
                          }}
                          className="w-full px-3 py-2 text-left text-sm text-red-600 hover:bg-red-50 flex items-center gap-2"
                        >
                          <svg className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                          </svg>
                          Delete Stage
                        </button>
                      )}
                    </div>
                  </>
                )}
              </div>
            )}
          </div>
        </div>
        
        <div className="flex items-center justify-between text-sm">
          <span className="text-gray-500">
            {formatCurrency(totalValue)}
          </span>
          <span className="text-gray-400">
            {stage.winProbability}% prob.
          </span>
        </div>
      </div>
      
      {/* Opportunities List */}
      <div
        ref={setNodeRef}
        className="flex-1 p-2 overflow-y-auto min-h-[200px]"
      >
        <SortableContext
          items={stage.deals.map(o => o.id)}
          strategy={verticalListSortingStrategy}
        >
          {stage.deals.map(opportunity => (
            <div key={opportunity.id} className="group">
              <OpportunityCard
                opportunity={opportunity}
                onEdit={onEditOpportunity}
                onDelete={onDeleteOpportunity}
              />
            </div>
          ))}
        </SortableContext>
        
        {stage.deals.length === 0 && (
          <div className="text-center py-8 text-gray-400">
            <p className="text-sm">No opportunities in this stage</p>
          </div>
        )}
      </div>
      
      {/* Add Opportunity Button */}
      <div className="p-2 border-t border-gray-200">
        <button
          onClick={onAddOpportunity}
          className="w-full py-2 text-sm text-gray-500 hover:text-green-600 hover:bg-green-50 rounded-lg transition-colors flex items-center justify-center gap-1"
        >
          <span>+</span>
          <span>Add Opportunity</span>
        </button>
      </div>
    </div>
  )
}
