'use client'

export const dynamic = 'force-dynamic'

import { useState, Suspense, useEffect, useMemo } from 'react'
import Link from 'next/link'
import { useSearchParams } from 'next/navigation'
import { DndContext, DragEndEvent, DragOverlay, DragStartEvent, closestCorners, PointerSensor, useSensor, useSensors } from '@dnd-kit/core'
import { SortableContext, verticalListSortingStrategy } from '@dnd-kit/sortable'
import OpportunityCard from '@/components/lead-engine/OpportunityCard'
import StageColumn from '@/components/lead-engine/StageColumn'
import OpportunityModal from '@/components/lead-engine/OpportunityModal'
import PipelineSelector from '@/components/lead-engine/PipelineSelector'
import StageModal from '@/components/lead-engine/StageModal'
import { PageHeader } from '@/components/ui/PageHeader'

// ==========================================
// RISIVO CRM - Lead Engine Module
// Sales pipeline management with tabbed interface
// ==========================================

// Tab Configuration
const LEAD_ENGINE_TABS = [
  { id: 'pipeline', label: 'Lead Engine', icon: 'fa-rocket', href: '/sub-account/lead-engine' },
  { id: 'manage', label: 'Manage Engines', icon: 'fa-cogs', href: '/sub-account/lead-engine?tab=manage' },
  { id: 'bulk-actions', label: 'Bulk Actions', icon: 'fa-tasks', href: '/sub-account/lead-engine?tab=bulk-actions' },
]

// ==========================================
// TYPE DEFINITIONS
// ==========================================

interface Contact {
  id: string
  firstName: string
  lastName: string
  email: string
  phone?: string
}

interface Stage {
  id: string
  name: string
  color: string
  stageType: 'open' | 'won' | 'lost'
  winProbability: number
  displayOrder: number
  deals: Opportunity[]
}

interface Opportunity {
  id: string
  name: string
  description?: string
  value: number
  currency: string
  status: string
  priority: string
  probability: number
  expectedCloseDate?: string
  contact?: Contact
  stageId: string
  pipelineId: string
  tags: string[]
  lastActivityAt: string
  createdAt: string
}

interface Pipeline {
  id: string
  name: string
  icon: string
  color: string
  isDefault: boolean
  stages: Stage[]
  totalOpportunities?: number
  totalValue?: number
  description?: string
  createdAt?: string
  status?: 'active' | 'archived'
}

type TabType = 'pipeline' | 'manage' | 'bulk-actions'

// ==========================================
// MAIN PAGE COMPONENT
// ==========================================

function LeadEnginePageContent() {
  const searchParams = useSearchParams()
  const activeTab = (searchParams.get('tab') as TabType) || 'pipeline'

  const [pipelines, setPipelines] = useState<Pipeline[]>([])
  const [selectedPipeline, setSelectedPipeline] = useState<Pipeline | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  
  // Modal states
  const [isOpportunityModalOpen, setIsOpportunityModalOpen] = useState(false)
  const [editingOpportunity, setEditingOpportunity] = useState<Opportunity | null>(null)
  const [defaultStageId, setDefaultStageId] = useState<string | null>(null)
  
  // Stage modal states
  const [isStageModalOpen, setIsStageModalOpen] = useState(false)
  const [editingStage, setEditingStage] = useState<Stage | null>(null)
  
  // Drag state
  const [activeOpportunity, setActiveOpportunity] = useState<Opportunity | null>(null)
  
  // TODO: Get from auth context
  const subAccountId = 'sub-001'
  
  const sensors = useSensors(
    useSensor(PointerSensor, {
      activationConstraint: {
        distance: 8,
      },
    })
  )
  
  // Fetch pipelines with stages and deals
  const fetchPipelines = async () => {
    try {
      setLoading(true)
      const response = await fetch(
        `/api/v1/pipelines?subAccountId=${subAccountId}&includeStages=true&includeDeals=true`
      )
      const result = await response.json()
      
      if (!result.success) throw new Error(result.error)
      
      setPipelines(result.data)
      
      // Select default pipeline or first one
      if (result.data.length > 0) {
        const defaultPipeline = result.data.find((p: Pipeline) => p.isDefault) || result.data[0]
        setSelectedPipeline(defaultPipeline)
      }
    } catch (err: any) {
      setError(err.message)
    } finally {
      setLoading(false)
    }
  }
  
  useEffect(() => {
    fetchPipelines()
  }, [])
  
  // Handle drag start
  const handleDragStart = (event: DragStartEvent) => {
    const { active } = event
    const deal = findOpportunityById(active.id as string)
    setActiveOpportunity(opportunity || null)
  }
  
  // Handle drag end - move opportunity to new stage
  const handleDragEnd = async (event: DragEndEvent) => {
    const { active, over } = event
    setActiveOpportunity(null)
    
    if (!over || !selectedPipeline) return
    
    const opportunityId = active.id as string
    const newStageId = over.id as string
    
    // Find the deal and current stage
    const deal = findOpportunityById(opportunityId)
    if (!deal || deal.stageId === newStageId) return
    
    // Optimistic update
    const updatedPipeline = { ...selectedPipeline }
    updatedPipeline.stages = updatedPipeline.stages.map(stage => {
      if (stage.id === deal.stageId) {
        return { ...stage, opportunities: stage.deals.filter(d => d.id !== opportunityId) }
      }
      if (stage.id === newStageId) {
        return { ...stage, opportunities: [...stage.deals, { ...deal, stageId: newStageId }] }
      }
      return stage
    })
    setSelectedPipeline(updatedPipeline)
    
    // API call
    try {
      const response = await fetch(`/api/v1/deals/${opportunityId}`, {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ stageId: newStageId }),
      })
      
      const result = await response.json()
      if (!result.success) throw new Error(result.error)
      
      // Refresh to get accurate data
      fetchPipelines()
    } catch (err: any) {
      console.error('Failed to move opportunity:', err)
      // Revert on error
      fetchPipelines()
    }
  }
  
  // Find deal by ID across all stages
  const findOpportunityById = (opportunityId: string): Opportunity | undefined => {
    if (!selectedPipeline) return undefined
    for (const stage of selectedPipeline.stages) {
      const deal = stage.deals.find(d => d.id === opportunityId)
      if (deal) return deal
    }
    return undefined
  }
  
  // Handle create opportunity
  const handleCreateOpportunity = (stageId?: string) => {
    setEditingOpportunity(null)
    setDefaultStageId(stageId || null)
    setIsOpportunityModalOpen(true)
  }
  
  // Handle edit opportunity
  const handleEditOpportunity = (opportunity: Opportunity) => {
    setEditingOpportunity(opportunity)
    setDefaultStageId(null)
    setIsOpportunityModalOpen(true)
  }
  
  // Handle deal saved
  const handleOpportunitySaved = () => {
    setIsOpportunityModalOpen(false)
    setEditingOpportunity(null)
    setDefaultStageId(null)
    fetchPipelines()
  }
  
  // Handle opportunity deleted
  const handleDeleteOpportunity = async (opportunityId: string) => {
    if (!confirm('Are you sure you want to delete this opportunity?')) return
    
    try {
      const response = await fetch(`/api/v1/deals/${opportunityId}`, {
        method: 'DELETE',
      })
      
      const result = await response.json()
      if (!result.success) throw new Error(result.error)
      
      fetchPipelines()
    } catch (err: any) {
      alert('Failed to delete deal: ' + err.message)
    }
  }
  
  // Handle create stage
  const handleCreateStage = () => {
    setEditingStage(null)
    setIsStageModalOpen(true)
  }
  
  // Handle edit stage
  const handleEditStage = (stage: Stage) => {
    setEditingStage(stage)
    setIsStageModalOpen(true)
  }
  
  // Handle stage saved
  const handleStageSaved = () => {
    setIsStageModalOpen(false)
    setEditingStage(null)
    fetchPipelines()
  }
  
  // Handle delete stage
  const handleDeleteStage = async (stageId: string) => {
    if (!selectedPipeline) return
    
    try {
      const response = await fetch(
        `/api/v1/pipelines/${selectedPipeline.id}/stages/${stageId}`,
        { method: 'DELETE' }
      )
      
      const result = await response.json()
      if (!result.success) throw new Error(result.error)
      
      fetchPipelines()
    } catch (err: any) {
      alert('Failed to delete stage: ' + err.message)
    }
  }
  
  // Calculate pipeline stats
  const calculateStats = () => {
    if (!selectedPipeline) return { totalOpportunities: 0, totalValue: 0, wonValue: 0, openOpportunities: 0 }
    
    let totalOpportunities = 0
    let totalValue = 0
    let wonValue = 0
    let openOpportunities = 0
    
    selectedPipeline.stages.forEach(stage => {
      stage.deals.forEach(deal => {
        totalOpportunities++
        totalValue += deal.value || 0
        if (stage.stageType === 'won') {
          wonValue += deal.value || 0
        }
        if (stage.stageType === 'open') {
          openOpportunities++
        }
      })
    })
    
    return { totalOpportunities, totalValue, wonValue, openOpportunities }
  }
  
  const stats = calculateStats()

  return (
    <div className="space-y-6 -m-4 sm:-m-6 lg:-m-8">
      {/* Page Header with Tabs */}
      <div className="flex-none bg-white border-b border-gray-200 px-4 sm:px-6 lg:px-8 pt-4 sm:pt-6 lg:pt-8 pb-0">
        <PageHeader
          title="Lead Engine"
          description="Manage sales pipelines, track opportunities, and drive revenue growth"
          icon="fa-rocket"
        />

        {/* Tab Navigation */}
        <nav className="-mb-px flex space-x-8" aria-label="Lead Engine tabs">
          {LEAD_ENGINE_TABS.map((tab) => {
            const isActive = tab.id === activeTab
            
            return (
              <Link
                key={tab.id}
                href={tab.href}
                className={`group inline-flex items-center border-b-2 py-4 px-1 text-sm font-medium transition-colors ${
                  isActive
                    ? 'border-[var(--theme-secondary)] text-[var(--theme-secondary)]'
                    : 'border-transparent text-gray-500 hover:border-gray-300 hover:text-gray-700'
                }`}
              >
                <i className={`fas ${tab.icon} mr-2 text-base`} style={{ color: isActive ? 'var(--theme-secondary)' : undefined }}></i>
                {tab.label}
              </Link>
            )
          })}
        </nav>
      </div>

      {/* Tab Content - wrapped in padding container */}
      <div className="px-4 sm:px-6 lg:px-8 pb-4 sm:pb-6 lg:pb-8">
        {activeTab === 'pipeline' && (
          <LeadEngineTab
            pipelines={pipelines}
            selectedPipeline={selectedPipeline}
            setSelectedPipeline={setSelectedPipeline}
            loading={loading}
            error={error}
            stats={stats}
            sensors={sensors}
            activeOpportunity={activeOpportunity}
            subAccountId={subAccountId}
            onDragStart={handleDragStart}
            onDragEnd={handleDragEnd}
            onCreateOpportunity={handleCreateOpportunity}
            onEditOpportunity={handleEditOpportunity}
            onDeleteOpportunity={handleDeleteOpportunity}
            onCreateStage={handleCreateStage}
            onEditStage={handleEditStage}
            onDeleteStage={handleDeleteStage}
            fetchPipelines={fetchPipelines}
          />
        )}

        {activeTab === 'manage' && (
          <ManageEnginesTab
            pipelines={pipelines}
            loading={loading}
            subAccountId={subAccountId}
            onPipelineUpdated={fetchPipelines}
          />
        )}

        {activeTab === 'bulk-actions' && (
          <BulkActionsTab
            pipelines={pipelines}
            selectedPipeline={selectedPipeline}
            loading={loading}
            onRefresh={fetchPipelines}
          />
        )}
      </div>

      {/* Opportunity Modal */}
      <OpportunityModal
        isOpen={isOpportunityModalOpen}
        onClose={() => {
          setIsOpportunityModalOpen(false)
          setEditingOpportunity(null)
          setDefaultStageId(null)
        }}
        onSave={handleOpportunitySaved}
        deal={editingOpportunity}
        pipeline={selectedPipeline}
        defaultStageId={defaultStageId}
        subAccountId={subAccountId}
      />
      
      {/* Stage Modal */}
      {selectedPipeline && (
        <StageModal
          isOpen={isStageModalOpen}
          onClose={() => {
            setIsStageModalOpen(false)
            setEditingStage(null)
          }}
          onSave={handleStageSaved}
          stage={editingStage}
          pipelineId={selectedPipeline.id}
          subAccountId={subAccountId}
          existingStagesCount={selectedPipeline.stages.length}
        />
      )}
    </div>
  )
}

// ==========================================
// LEAD ENGINE TAB (Kanban View)
// ==========================================

function LeadEngineTab({
  pipelines,
  selectedPipeline,
  setSelectedPipeline,
  loading,
  error,
  stats,
  sensors,
  activeOpportunity,
  subAccountId,
  onDragStart,
  onDragEnd,
  onCreateOpportunity,
  onEditOpportunity,
  onDeleteOpportunity,
  onCreateStage,
  onEditStage,
  onDeleteStage,
  fetchPipelines,
}: {
  pipelines: Pipeline[]
  selectedPipeline: Pipeline | null
  setSelectedPipeline: (pipeline: Pipeline | null) => void
  loading: boolean
  error: string | null
  stats: { totalOpportunities: number; totalValue: number; wonValue: number; openOpportunities: number }
  sensors: any
  activeOpportunity: Opportunity | null
  subAccountId: string
  onDragStart: (event: DragStartEvent) => void
  onDragEnd: (event: DragEndEvent) => void
  onCreateOpportunity: (stageId?: string) => void
  onEditOpportunity: (deal: Opportunity) => void
  onDeleteOpportunity: (opportunityId: string) => void
  onCreateStage: () => void
  onEditStage: (stage: Stage) => void
  onDeleteStage: (stageId: string) => void
  fetchPipelines: () => void
}) {
  if (loading) {
    return (
      <div className="flex items-center justify-center h-96">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-green-500" />
      </div>
    )
  }
  
  if (error) {
    return (
      <div className="rounded-lg p-6 text-center bg-red-50 border border-red-200">
        <p className="text-red-600">{error}</p>
        <button
          onClick={fetchPipelines}
          className="mt-4 px-4 py-2 text-white rounded-lg hover:opacity-90 transition-opacity bg-red-600"
        >
          Retry
        </button>
      </div>
    )
  }

  return (
    <div className="h-full flex flex-col">
      {/* Header */}
      <div className="flex items-center justify-between mb-6">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">üéØ Pipeline View</h1>
          <p className="mt-1 text-gray-500">Drag and drop opportunities between stages</p>
        </div>
        
        <div className="flex items-center gap-4">
          {/* Pipeline Selector */}
          <PipelineSelector
            pipelines={pipelines}
            selectedPipeline={selectedPipeline}
            onSelect={(pipeline) => setSelectedPipeline(pipeline as Pipeline | null)}
            onCreatePipeline={fetchPipelines}
            subAccountId={subAccountId}
          />
          
          {/* Add Opportunity Button */}
          <button
            onClick={() => onCreateOpportunity()}
            className="flex items-center gap-2 px-4 py-2 rounded-lg hover:opacity-90 transition-opacity bg-green-600 text-white"
          >
            <span className="text-xl">+</span>
            <span>Add Opportunity</span>
          </button>
        </div>
      </div>
      
      {/* Stats Bar */}
      <div className="grid grid-cols-4 gap-4 mb-6">
        <div className="rounded-lg shadow-sm p-4 bg-white border border-gray-200">
          <p className="text-sm text-gray-500">Total Opportunities</p>
          <p className="text-2xl font-bold text-gray-900">{stats.totalOpportunities}</p>
        </div>
        <div className="rounded-lg shadow-sm p-4 bg-white border border-gray-200">
          <p className="text-sm text-gray-500">Open Opportunities</p>
          <p className="text-2xl font-bold text-blue-600">{stats.openOpportunities}</p>
        </div>
        <div className="rounded-lg shadow-sm p-4 bg-white border border-gray-200">
          <p className="text-sm text-gray-500">Pipeline Value</p>
          <p className="text-2xl font-bold text-gray-900">
            ${stats.totalValue.toLocaleString()}
          </p>
        </div>
        <div className="rounded-lg shadow-sm p-4 bg-white border border-gray-200">
          <p className="text-sm text-gray-500">Won Value</p>
          <p className="text-2xl font-bold text-green-600">
            ${stats.wonValue.toLocaleString()}
          </p>
        </div>
      </div>
      
      {/* Kanban Board */}
      {selectedPipeline ? (
        <DndContext
          sensors={sensors}
          collisionDetection={closestCorners}
          onDragStart={onDragStart}
          onDragEnd={onDragEnd}
        >
          <div className="flex-1 overflow-x-auto">
            <div className="flex gap-4 h-full min-w-max pb-4">
              {selectedPipeline.stages
                .sort((a, b) => a.displayOrder - b.displayOrder)
                .filter(stage => stage.stageType !== 'lost')
                .map(stage => (
                  <StageColumn
                    key={stage.id}
                    stage={stage}
                    onAddOpportunity={() => onCreateOpportunity(stage.id)}
                    onEditOpportunity={onEditOpportunity}
                    onDeleteOpportunity={onDeleteOpportunity}
                    onEditStage={onEditStage}
                    onDeleteStage={onDeleteStage}
                  />
                ))}
              
              {/* Add Stage Button */}
              <div className="w-72 flex-shrink-0">
                <button
                  onClick={onCreateStage}
                  className="w-full h-full min-h-[200px] border-2 border-dashed border-gray-300 rounded-lg flex flex-col items-center justify-center gap-2 text-gray-400 hover:text-gray-600 hover:border-gray-400 hover:bg-gray-50 transition-colors"
                >
                  <svg className="w-8 h-8" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
                  </svg>
                  <span className="font-medium">Add Stage</span>
                </button>
              </div>
              
              {/* Show collapsed Lost column */}
              {selectedPipeline.stages.filter(s => s.stageType === 'lost').map(stage => (
                <div
                  key={stage.id}
                  className="w-16 flex-shrink-0 bg-gray-100 rounded-lg p-2 flex flex-col items-center cursor-pointer hover:bg-gray-200 transition-colors"
                  onClick={() => onEditStage(stage)}
                  title="Click to edit"
                >
                  <div
                    className="w-3 h-3 rounded-full mb-2"
                    style={{ backgroundColor: stage.color }}
                  />
                  <span className="text-xs text-gray-500 writing-mode-vertical transform rotate-180" style={{ writingMode: 'vertical-rl' }}>
                    {stage.name} ({stage.deals.length})
                  </span>
                </div>
              ))}
            </div>
          </div>
          
          {/* Drag Overlay */}
          <DragOverlay>
            {activeOpportunity ? (
              <OpportunityCard opportunity={activeOpportunity} isDragging />
            ) : null}
          </DragOverlay>
        </DndContext>
      ) : (
        <div className="flex-1 flex items-center justify-center bg-gray-50 rounded-lg">
          <div className="text-center">
            <p className="text-gray-500 text-lg mb-4">No pipeline selected</p>
            <p className="text-gray-400">Create a pipeline to get started</p>
          </div>
        </div>
      )}
    </div>
  )
}

// ==========================================
// MANAGE ENGINES TAB
// ==========================================

function ManageEnginesTab({
  pipelines,
  loading,
  subAccountId,
  onPipelineUpdated,
}: {
  pipelines: Pipeline[]
  loading: boolean
  subAccountId: string
  onPipelineUpdated: () => void
}) {
  const [showCreateModal, setShowCreateModal] = useState(false)
  const [editingPipeline, setEditingPipeline] = useState<Pipeline | null>(null)
  const [pipelineForm, setPipelineForm] = useState({
    name: '',
    description: '',
    icon: 'üéØ',
    color: '#10B981',
    isDefault: false,
  })

  const pipelineIcons = ['üéØ', 'üí∞', 'üöÄ', '‚≠ê', 'üî•', 'üíé', 'üèÜ', 'üìà', 'üé™', 'üé®', '‚ö°', 'üåü']
  const pipelineColors = ['#10B981', '#3B82F6', '#8B5CF6', '#F59E0B', '#EF4444', '#EC4899', '#06B6D4', '#84CC16']

  const handleCreatePipeline = () => {
    setEditingPipeline(null)
    setPipelineForm({
      name: '',
      description: '',
      icon: 'üéØ',
      color: '#10B981',
      isDefault: false,
    })
    setShowCreateModal(true)
  }

  const handleEditPipeline = (pipeline: Pipeline) => {
    setEditingPipeline(pipeline)
    setPipelineForm({
      name: pipeline.name,
      description: pipeline.description || '',
      icon: pipeline.icon || 'üéØ',
      color: pipeline.color || '#10B981',
      isDefault: pipeline.isDefault,
    })
    setShowCreateModal(true)
  }

  const handleSavePipeline = async () => {
    if (!pipelineForm.name.trim()) return

    try {
      if (editingPipeline) {
        // Update existing pipeline
        const response = await fetch(`/api/v1/pipelines/${editingPipeline.id}`, {
          method: 'PATCH',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(pipelineForm),
        })
        const result = await response.json()
        if (!result.success) throw new Error(result.error)
      } else {
        // Create new pipeline
        const response = await fetch('/api/v1/pipelines', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            ...pipelineForm,
            subAccountId,
          }),
        })
        const result = await response.json()
        if (!result.success) throw new Error(result.error)
      }

      setShowCreateModal(false)
      onPipelineUpdated()
    } catch (err: any) {
      alert('Failed to save pipeline: ' + err.message)
    }
  }

  const handleDeletePipeline = async (pipelineId: string) => {
    if (!confirm('Are you sure you want to delete this pipeline? All opportunities will be moved to another pipeline or deleted.')) {
      return
    }

    try {
      const response = await fetch(`/api/v1/pipelines/${pipelineId}`, {
        method: 'DELETE',
      })
      const result = await response.json()
      if (!result.success) throw new Error(result.error)
      onPipelineUpdated()
    } catch (err: any) {
      alert('Failed to delete pipeline: ' + err.message)
    }
  }

  const handleSetDefault = async (pipelineId: string) => {
    try {
      const response = await fetch(`/api/v1/pipelines/${pipelineId}`, {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ isDefault: true }),
      })
      const result = await response.json()
      if (!result.success) throw new Error(result.error)
      onPipelineUpdated()
    } catch (err: any) {
      alert('Failed to set default: ' + err.message)
    }
  }

  const handleDuplicatePipeline = async (pipeline: Pipeline) => {
    try {
      const response = await fetch('/api/v1/pipelines', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          name: `${pipeline.name} (Copy)`,
          description: pipeline.description,
          icon: pipeline.icon,
          color: pipeline.color,
          isDefault: false,
          subAccountId,
          // Copy stages structure
          stages: pipeline.stages.map(s => ({
            name: s.name,
            color: s.color,
            stageType: s.stageType,
            winProbability: s.winProbability,
            displayOrder: s.displayOrder,
          })),
        }),
      })
      const result = await response.json()
      if (!result.success) throw new Error(result.error)
      onPipelineUpdated()
    } catch (err: any) {
      alert('Failed to duplicate pipeline: ' + err.message)
    }
  }

  // Calculate stats for each pipeline
  const getPipelineStats = (pipeline: Pipeline) => {
    let totalOpportunities = 0
    let totalValue = 0
    let openOpportunities = 0
    let wonOpportunities = 0

    pipeline.stages.forEach(stage => {
      totalOpportunities += stage.deals.length
      stage.deals.forEach(deal => {
        totalValue += deal.value || 0
        if (stage.stageType === 'open') openOpportunities++
        if (stage.stageType === 'won') wonOpportunities++
      })
    })

    return { totalOpportunities, totalValue, openOpportunities, wonOpportunities, stageCount: pipeline.stages.length }
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center h-96">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-green-500" />
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold text-gray-900">‚öôÔ∏è Manage Engines</h2>
          <p className="mt-1 text-gray-500">Create and configure your sales pipelines</p>
        </div>
        <button
          onClick={handleCreatePipeline}
          className="flex items-center gap-2 px-4 py-2 rounded-lg bg-green-600 text-white hover:bg-green-700 transition-colors"
        >
          <i className="fas fa-plus"></i>
          <span>Create Pipeline</span>
        </button>
      </div>

      {/* Pipelines Grid */}
      {pipelines.length === 0 ? (
        <div className="bg-white rounded-lg border border-gray-200 p-12 text-center">
          <i className="fas fa-rocket text-5xl text-gray-300 mb-4"></i>
          <h3 className="text-lg font-medium text-gray-900 mb-2">No pipelines yet</h3>
          <p className="text-gray-500 mb-4">Create your first pipeline to start tracking opportunities</p>
          <button
            onClick={handleCreatePipeline}
            className="px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700"
          >
            Create Pipeline
          </button>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {pipelines.map(pipeline => {
            const pipelineStats = getPipelineStats(pipeline)
            return (
              <div
                key={pipeline.id}
                className="bg-white rounded-lg border border-gray-200 overflow-hidden hover:shadow-lg transition-shadow"
              >
                {/* Header */}
                <div 
                  className="p-4 border-b border-gray-200"
                  style={{ backgroundColor: `${pipeline.color}15` }}
                >
                  <div className="flex items-start justify-between">
                    <div className="flex items-center gap-3">
                      <span className="text-3xl">{pipeline.icon || 'üéØ'}</span>
                      <div>
                        <h3 className="font-semibold text-gray-900">{pipeline.name}</h3>
                        {pipeline.isDefault && (
                          <span className="text-xs bg-yellow-100 text-yellow-700 px-2 py-0.5 rounded-full">
                            Default
                          </span>
                        )}
                      </div>
                    </div>
                    <div className="relative group">
                      <button className="p-2 text-gray-400 hover:text-gray-600 rounded-lg hover:bg-white/50">
                        <i className="fas fa-ellipsis-v"></i>
                      </button>
                      <div className="absolute right-0 mt-1 w-48 bg-white rounded-lg shadow-lg border border-gray-200 py-1 z-10 hidden group-hover:block">
                        <button
                          onClick={() => handleEditPipeline(pipeline)}
                          className="w-full px-4 py-2 text-left text-sm text-gray-700 hover:bg-gray-100 flex items-center gap-2"
                        >
                          <i className="fas fa-edit w-4"></i>
                          Edit Pipeline
                        </button>
                        {!pipeline.isDefault && (
                          <button
                            onClick={() => handleSetDefault(pipeline.id)}
                            className="w-full px-4 py-2 text-left text-sm text-gray-700 hover:bg-gray-100 flex items-center gap-2"
                          >
                            <i className="fas fa-star w-4"></i>
                            Set as Default
                          </button>
                        )}
                        <button
                          onClick={() => handleDuplicatePipeline(pipeline)}
                          className="w-full px-4 py-2 text-left text-sm text-gray-700 hover:bg-gray-100 flex items-center gap-2"
                        >
                          <i className="fas fa-copy w-4"></i>
                          Duplicate
                        </button>
                        <hr className="my-1" />
                        <button
                          onClick={() => handleDeletePipeline(pipeline.id)}
                          className="w-full px-4 py-2 text-left text-sm text-red-600 hover:bg-red-50 flex items-center gap-2"
                        >
                          <i className="fas fa-trash w-4"></i>
                          Delete
                        </button>
                      </div>
                    </div>
                  </div>
                  {pipeline.description && (
                    <p className="text-sm text-gray-600 mt-2">{pipeline.description}</p>
                  )}
                </div>

                {/* Stats */}
                <div className="p-4 space-y-3">
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <p className="text-xs text-gray-500">Total Opportunities</p>
                      <p className="text-lg font-semibold text-gray-900">{pipelineStats.totalOpportunities}</p>
                    </div>
                    <div>
                      <p className="text-xs text-gray-500">Pipeline Value</p>
                      <p className="text-lg font-semibold text-gray-900">${pipelineStats.totalValue.toLocaleString()}</p>
                    </div>
                    <div>
                      <p className="text-xs text-gray-500">Open Opportunities</p>
                      <p className="text-lg font-semibold text-blue-600">{pipelineStats.openOpportunities}</p>
                    </div>
                    <div>
                      <p className="text-xs text-gray-500">Won Opportunities</p>
                      <p className="text-lg font-semibold text-green-600">{pipelineStats.wonOpportunities}</p>
                    </div>
                  </div>

                  {/* Stages Preview */}
                  <div className="pt-3 border-t border-gray-200">
                    <p className="text-xs text-gray-500 mb-2">Stages ({pipelineStats.stageCount})</p>
                    <div className="flex flex-wrap gap-1">
                      {pipeline.stages
                        .sort((a, b) => a.displayOrder - b.displayOrder)
                        .slice(0, 5)
                        .map(stage => (
                          <span
                            key={stage.id}
                            className="text-xs px-2 py-1 rounded-full"
                            style={{ 
                              backgroundColor: `${stage.color}20`,
                              color: stage.color
                            }}
                          >
                            {stage.name}
                          </span>
                        ))}
                      {pipeline.stages.length > 5 && (
                        <span className="text-xs px-2 py-1 bg-gray-100 text-gray-500 rounded-full">
                          +{pipeline.stages.length - 5} more
                        </span>
                      )}
                    </div>
                  </div>
                </div>

                {/* Actions */}
                <div className="px-4 py-3 bg-gray-50 border-t border-gray-200 flex justify-between">
                  <Link
                    href={`/sub-account/lead-engine`}
                    className="text-sm text-green-600 hover:text-green-700 flex items-center gap-1"
                  >
                    <i className="fas fa-eye"></i>
                    View Pipeline
                  </Link>
                  <button
                    onClick={() => handleEditPipeline(pipeline)}
                    className="text-sm text-gray-600 hover:text-gray-700 flex items-center gap-1"
                  >
                    <i className="fas fa-cog"></i>
                    Configure
                  </button>
                </div>
              </div>
            )
          })}
        </div>
      )}

      {/* Create/Edit Pipeline Modal */}
      {showCreateModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50">
          <div className="bg-white rounded-xl shadow-xl w-full max-w-lg mx-4 max-h-[90vh] overflow-y-auto">
            <div className="p-6">
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-xl font-bold text-gray-900">
                  {editingPipeline ? 'Edit Pipeline' : 'Create Pipeline'}
                </h2>
                <button 
                  onClick={() => setShowCreateModal(false)} 
                  className="text-gray-400 hover:text-gray-600"
                >
                  <i className="fas fa-times text-xl"></i>
                </button>
              </div>

              <div className="space-y-4">
                {/* Name */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Pipeline Name *
                  </label>
                  <input
                    type="text"
                    value={pipelineForm.name}
                    onChange={(e) => setPipelineForm(prev => ({ ...prev, name: e.target.value }))}
                    placeholder="e.g., Sales Pipeline"
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                  />
                </div>

                {/* Description */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Description
                  </label>
                  <textarea
                    value={pipelineForm.description}
                    onChange={(e) => setPipelineForm(prev => ({ ...prev, description: e.target.value }))}
                    placeholder="Brief description..."
                    rows={2}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                  />
                </div>

                {/* Icon */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Icon</label>
                  <div className="flex flex-wrap gap-2">
                    {pipelineIcons.map(icon => (
                      <button
                        key={icon}
                        type="button"
                        onClick={() => setPipelineForm(prev => ({ ...prev, icon }))}
                        className={`w-10 h-10 text-xl flex items-center justify-center rounded-lg border-2 transition-colors ${
                          pipelineForm.icon === icon
                            ? 'border-green-500 bg-green-50'
                            : 'border-gray-200 hover:border-gray-300'
                        }`}
                      >
                        {icon}
                      </button>
                    ))}
                  </div>
                </div>

                {/* Color */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Color</label>
                  <div className="flex flex-wrap gap-2">
                    {pipelineColors.map(color => (
                      <button
                        key={color}
                        type="button"
                        onClick={() => setPipelineForm(prev => ({ ...prev, color }))}
                        className={`w-10 h-10 rounded-lg border-2 transition-all ${
                          pipelineForm.color === color
                            ? 'border-gray-900 scale-110'
                            : 'border-transparent hover:scale-105'
                        }`}
                        style={{ backgroundColor: color }}
                      />
                    ))}
                  </div>
                </div>

                {/* Default Toggle */}
                <div className="flex items-center justify-between pt-4 border-t border-gray-200">
                  <div>
                    <h4 className="text-sm font-medium text-gray-900">Set as Default</h4>
                    <p className="text-sm text-gray-500">This pipeline will be selected by default</p>
                  </div>
                  <button
                    type="button"
                    onClick={() => setPipelineForm(prev => ({ ...prev, isDefault: !prev.isDefault }))}
                    className={`relative inline-flex h-6 w-11 flex-shrink-0 cursor-pointer rounded-full border-2 border-transparent transition-colors duration-200 ease-in-out focus:outline-none ${
                      pipelineForm.isDefault ? 'bg-green-600' : 'bg-gray-200'
                    }`}
                  >
                    <span
                      className={`pointer-events-none inline-block h-5 w-5 transform rounded-full bg-white shadow ring-0 transition duration-200 ease-in-out ${
                        pipelineForm.isDefault ? 'translate-x-5' : 'translate-x-0'
                      }`}
                    />
                  </button>
                </div>
              </div>

              <div className="flex gap-3 mt-6">
                <button
                  onClick={() => setShowCreateModal(false)}
                  className="flex-1 px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50"
                >
                  Cancel
                </button>
                <button
                  onClick={handleSavePipeline}
                  disabled={!pipelineForm.name.trim()}
                  className="flex-1 px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 disabled:opacity-50"
                >
                  {editingPipeline ? 'Save Changes' : 'Create Pipeline'}
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}

// ==========================================
// BULK ACTIONS TAB
// ==========================================

function BulkActionsTab({
  pipelines,
  selectedPipeline,
  loading,
  onRefresh,
}: {
  pipelines: Pipeline[]
  selectedPipeline: Pipeline | null
  loading: boolean
  onRefresh: () => void
}) {
  const [selectedOpportunities, setSelectedOpportunities] = useState<string[]>([])
  const [pipelineFilter, setPipelineFilter] = useState<string>('')
  const [stageFilter, setStageFilter] = useState<string>('')
  const [search, setSearch] = useState('')
  const [showActionModal, setShowActionModal] = useState(false)
  const [actionType, setActionType] = useState<string>('')
  const [actionValue, setActionValue] = useState<string>('')
  const [processing, setProcessing] = useState(false)

  // Get all opportunities across all pipelines
  const allOpportunities = useMemo(() => {
    const opportunities: (Opportunity & { pipelineName: string; stageName: string; stageColor: string })[] = []
    const targetPipelines = pipelineFilter ? pipelines.filter(p => p.id === pipelineFilter) : pipelines

    targetPipelines.forEach(pipeline => {
      pipeline.stages.forEach(stage => {
        if (stageFilter && stage.id !== stageFilter) return
        stage.deals.forEach(deal => {
          opportunities.push({
            ...deal,
            pipelineName: pipeline.name,
            stageName: stage.name,
            stageColor: stage.color,
          })
        })
      })
    })

    // Apply search
    if (search) {
      const searchLower = search.toLowerCase()
      return opportunities.filter(d => 
        d.name.toLowerCase().includes(searchLower) ||
        d.contact?.firstName?.toLowerCase().includes(searchLower) ||
        d.contact?.lastName?.toLowerCase().includes(searchLower) ||
        d.contact?.email?.toLowerCase().includes(searchLower)
      )
    }

    return opportunities
  }, [pipelines, pipelineFilter, stageFilter, search])

  // Get stages for selected pipeline filter
  const filteredStages = useMemo(() => {
    if (!pipelineFilter) return []
    const pipeline = pipelines.find(p => p.id === pipelineFilter)
    return pipeline?.stages || []
  }, [pipelineFilter, pipelines])

  const handleSelectAll = () => {
    if (selectedOpportunities.length === allOpportunities.length) {
      setSelectedOpportunities([])
    } else {
      setSelectedOpportunities(allOpportunities.map(d => d.id))
    }
  }

  const handleSelectOpportunity = (opportunityId: string) => {
    if (selectedOpportunities.includes(opportunityId)) {
      setSelectedOpportunities(prev => prev.filter(id => id !== opportunityId))
    } else {
      setSelectedOpportunities(prev => [...prev, opportunityId])
    }
  }

  const handleBulkAction = async () => {
    if (selectedOpportunities.length === 0 || !actionType) return

    setProcessing(true)
    try {
      // Process each deal
      for (const opportunityId of selectedOpportunities) {
        let updateData: any = {}

        switch (actionType) {
          case 'move-stage':
            updateData = { stageId: actionValue }
            break
          case 'update-status':
            updateData = { status: actionValue }
            break
          case 'update-priority':
            updateData = { priority: actionValue }
            break
          case 'delete':
            await fetch(`/api/v1/deals/${opportunityId}`, { method: 'DELETE' })
            continue
        }

        if (Object.keys(updateData).length > 0) {
          await fetch(`/api/v1/deals/${opportunityId}`, {
            method: 'PATCH',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(updateData),
          })
        }
      }

      setShowActionModal(false)
      setSelectedOpportunities([])
      setActionType('')
      setActionValue('')
      onRefresh()
      alert(`Successfully processed ${selectedOpportunities.length} deals`)
    } catch (err: any) {
      alert('Error processing opportunities: ' + err.message)
    } finally {
      setProcessing(false)
    }
  }

  const openActionModal = (type: string) => {
    setActionType(type)
    setActionValue('')
    setShowActionModal(true)
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center h-96">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-green-500" />
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold text-gray-900">üì¶ Bulk Actions</h2>
          <p className="mt-1 text-gray-500">Perform actions on multiple opportunities at once</p>
        </div>
        {selectedOpportunities.length > 0 && (
          <div className="flex items-center gap-2">
            <span className="text-sm text-gray-600">
              {selectedOpportunities.length} deal{selectedOpportunities.length > 1 ? 's' : ''} selected
            </span>
          </div>
        )}
      </div>

      {/* Action Buttons */}
      <div className="bg-white rounded-lg border border-gray-200 p-4">
        <div className="flex flex-wrap gap-3">
          <button
            onClick={() => openActionModal('move-stage')}
            disabled={selectedOpportunities.length === 0}
            className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed flex items-center gap-2"
          >
            <i className="fas fa-arrows-alt"></i>
            Move to Stage
          </button>
          <button
            onClick={() => openActionModal('update-status')}
            disabled={selectedOpportunities.length === 0}
            className="px-4 py-2 bg-purple-600 text-white rounded-lg hover:bg-purple-700 disabled:opacity-50 disabled:cursor-not-allowed flex items-center gap-2"
          >
            <i className="fas fa-flag"></i>
            Update Status
          </button>
          <button
            onClick={() => openActionModal('update-priority')}
            disabled={selectedOpportunities.length === 0}
            className="px-4 py-2 bg-yellow-600 text-white rounded-lg hover:bg-yellow-700 disabled:opacity-50 disabled:cursor-not-allowed flex items-center gap-2"
          >
            <i className="fas fa-exclamation-circle"></i>
            Update Priority
          </button>
          <button
            onClick={() => openActionModal('delete')}
            disabled={selectedOpportunities.length === 0}
            className="px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 disabled:opacity-50 disabled:cursor-not-allowed flex items-center gap-2"
          >
            <i className="fas fa-trash"></i>
            Delete Selected
          </button>
        </div>
      </div>

      {/* Filters */}
      <div className="bg-white rounded-lg border border-gray-200 p-4">
        <div className="flex flex-wrap gap-4 items-center">
          {/* Search */}
          <div className="flex-1 min-w-[200px]">
            <div className="relative">
              <i className="fas fa-search absolute left-3 top-1/2 -translate-y-1/2 text-gray-400"></i>
              <input
                type="text"
                placeholder="Search opportunities..."
                value={search}
                onChange={(e) => setSearch(e.target.value)}
                className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
              />
            </div>
          </div>

          {/* Pipeline Filter */}
          <select
            value={pipelineFilter}
            onChange={(e) => {
              setPipelineFilter(e.target.value)
              setStageFilter('')
            }}
            className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
          >
            <option value="">All Pipelines</option>
            {pipelines.map(pipeline => (
              <option key={pipeline.id} value={pipeline.id}>
                {pipeline.icon} {pipeline.name}
              </option>
            ))}
          </select>

          {/* Stage Filter */}
          <select
            value={stageFilter}
            onChange={(e) => setStageFilter(e.target.value)}
            disabled={!pipelineFilter}
            className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent disabled:opacity-50"
          >
            <option value="">All Stages</option>
            {filteredStages.map(stage => (
              <option key={stage.id} value={stage.id}>{stage.name}</option>
            ))}
          </select>

          {/* Refresh */}
          <button
            onClick={onRefresh}
            className="p-2 text-gray-500 hover:text-gray-700 hover:bg-gray-100 rounded-lg"
            title="Refresh"
          >
            <i className="fas fa-sync-alt"></i>
          </button>
        </div>
      </div>

      {/* Opportunities Table */}
      <div className="bg-white rounded-lg border border-gray-200 overflow-hidden">
        {allOpportunities.length === 0 ? (
          <div className="text-center py-12">
            <i className="fas fa-inbox text-4xl text-gray-300 mb-4"></i>
            <h3 className="text-lg font-medium text-gray-900 mb-1">No opportunities found</h3>
            <p className="text-gray-500">Try adjusting your filters</p>
          </div>
        ) : (
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-gray-50 border-b border-gray-200">
                <tr>
                  <th className="px-4 py-3 text-left">
                    <input
                      type="checkbox"
                      checked={selectedOpportunities.length === allOpportunities.length && allOpportunities.length > 0}
                      onChange={handleSelectAll}
                      className="rounded border-gray-300 text-green-600 focus:ring-green-500"
                    />
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Deal
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Contact
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Pipeline / Stage
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Value
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Priority
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Status
                  </th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-200">
                {allOpportunities.map(deal => (
                  <tr 
                    key={deal.id}
                    className={`hover:bg-gray-50 ${selectedOpportunities.includes(deal.id) ? 'bg-green-50' : ''}`}
                  >
                    <td className="px-4 py-3">
                      <input
                        type="checkbox"
                        checked={selectedOpportunities.includes(deal.id)}
                        onChange={() => handleSelectOpportunity(deal.id)}
                        className="rounded border-gray-300 text-green-600 focus:ring-green-500"
                      />
                    </td>
                    <td className="px-4 py-3">
                      <div className="font-medium text-gray-900">{deal.name}</div>
                      {deal.expectedCloseDate && (
                        <div className="text-xs text-gray-500">
                          Close: {new Date(deal.expectedCloseDate).toLocaleDateString()}
                        </div>
                      )}
                    </td>
                    <td className="px-4 py-3">
                      {deal.contact ? (
                        <div>
                          <div className="text-sm text-gray-900">
                            {deal.contact.firstName} {deal.contact.lastName}
                          </div>
                          <div className="text-xs text-gray-500">{deal.contact.email}</div>
                        </div>
                      ) : (
                        <span className="text-gray-400">No contact</span>
                      )}
                    </td>
                    <td className="px-4 py-3">
                      <div className="text-sm text-gray-900">{deal.pipelineName}</div>
                      <span
                        className="text-xs px-2 py-0.5 rounded-full"
                        style={{ 
                          backgroundColor: `${deal.stageColor}20`,
                          color: deal.stageColor
                        }}
                      >
                        {deal.stageName}
                      </span>
                    </td>
                    <td className="px-4 py-3">
                      <div className="font-medium text-gray-900">
                        ${(deal.value || 0).toLocaleString()}
                      </div>
                      <div className="text-xs text-gray-500">{deal.currency || 'USD'}</div>
                    </td>
                    <td className="px-4 py-3">
                      <span className={`px-2 py-1 text-xs font-medium rounded-full ${
                        deal.priority === 'high' ? 'bg-red-100 text-red-700' :
                        deal.priority === 'medium' ? 'bg-yellow-100 text-yellow-700' :
                        'bg-gray-100 text-gray-700'
                      }`}>
                        {deal.priority || 'normal'}
                      </span>
                    </td>
                    <td className="px-4 py-3">
                      <span className={`px-2 py-1 text-xs font-medium rounded-full ${
                        deal.status === 'active' ? 'bg-green-100 text-green-700' :
                        deal.status === 'won' ? 'bg-blue-100 text-blue-700' :
                        deal.status === 'lost' ? 'bg-red-100 text-red-700' :
                        'bg-gray-100 text-gray-700'
                      }`}>
                        {deal.status || 'active'}
                      </span>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>

      {/* Summary */}
      <div className="bg-gray-50 rounded-lg p-4 flex items-center justify-between text-sm">
        <span className="text-gray-600">
          Showing {allOpportunities.length} deal{allOpportunities.length !== 1 ? 's' : ''}
        </span>
        <span className="text-gray-900 font-medium">
          Total Value: ${allOpportunities.reduce((sum, d) => sum + (d.value || 0), 0).toLocaleString()}
        </span>
      </div>

      {/* Bulk Action Modal */}
      {showActionModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50">
          <div className="bg-white rounded-xl shadow-xl w-full max-w-md mx-4">
            <div className="p-6">
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-lg font-bold text-gray-900">
                  {actionType === 'move-stage' && 'Move to Stage'}
                  {actionType === 'update-status' && 'Update Status'}
                  {actionType === 'update-priority' && 'Update Priority'}
                  {actionType === 'delete' && 'Delete Opportunities'}
                </h3>
                <button 
                  onClick={() => setShowActionModal(false)}
                  className="text-gray-400 hover:text-gray-600"
                >
                  <i className="fas fa-times"></i>
                </button>
              </div>

              <p className="text-gray-600 mb-4">
                This action will affect {selectedOpportunities.length} deal{selectedOpportunities.length > 1 ? 's' : ''}.
              </p>

              {actionType === 'move-stage' && (
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Select Target Stage
                  </label>
                  <select
                    value={actionValue}
                    onChange={(e) => setActionValue(e.target.value)}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                  >
                    <option value="">Select stage...</option>
                    {pipelines.map(pipeline => (
                      <optgroup key={pipeline.id} label={`${pipeline.icon} ${pipeline.name}`}>
                        {pipeline.stages.map(stage => (
                          <option key={stage.id} value={stage.id}>{stage.name}</option>
                        ))}
                      </optgroup>
                    ))}
                  </select>
                </div>
              )}

              {actionType === 'update-status' && (
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Select Status
                  </label>
                  <select
                    value={actionValue}
                    onChange={(e) => setActionValue(e.target.value)}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                  >
                    <option value="">Select status...</option>
                    <option value="active">Active</option>
                    <option value="won">Won</option>
                    <option value="lost">Lost</option>
                    <option value="on-hold">On Hold</option>
                  </select>
                </div>
              )}

              {actionType === 'update-priority' && (
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Select Priority
                  </label>
                  <select
                    value={actionValue}
                    onChange={(e) => setActionValue(e.target.value)}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                  >
                    <option value="">Select priority...</option>
                    <option value="high">High</option>
                    <option value="medium">Medium</option>
                    <option value="low">Low</option>
                  </select>
                </div>
              )}

              {actionType === 'delete' && (
                <div className="p-4 bg-red-50 rounded-lg border border-red-200">
                  <p className="text-red-700 font-medium">‚ö†Ô∏è Warning</p>
                  <p className="text-red-600 text-sm mt-1">
                    This action cannot be undone. All selected opportunities will be permanently deleted.
                  </p>
                </div>
              )}

              <div className="flex gap-3 mt-6">
                <button
                  onClick={() => setShowActionModal(false)}
                  className="flex-1 px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50"
                >
                  Cancel
                </button>
                <button
                  onClick={handleBulkAction}
                  disabled={processing || (actionType !== 'delete' && !actionValue)}
                  className={`flex-1 px-4 py-2 text-white rounded-lg disabled:opacity-50 ${
                    actionType === 'delete' ? 'bg-red-600 hover:bg-red-700' : 'bg-green-600 hover:bg-green-700'
                  }`}
                >
                  {processing ? (
                    <>
                      <i className="fas fa-spinner fa-spin mr-2"></i>
                      Processing...
                    </>
                  ) : (
                    actionType === 'delete' ? 'Delete Opportunities' : 'Apply Changes'
                  )}
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}

export default function LeadEnginePage() {
  return (
    <Suspense fallback={<div className="min-h-screen bg-gray-50 dark:bg-[#0a0a0a] flex items-center justify-center"><div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div></div>}>
      <LeadEnginePageContent />
    </Suspense>
  )
}
