'use client'

import { useState } from 'react'

interface Stage {
  id: string
  name: string
  color: string
  stageType: 'open' | 'won' | 'lost'
  winProbability: number
  displayOrder: number
  deals: unknown[]
}

interface Pipeline {
  id: string
  name: string
  icon: string
  color: string
  isDefault: boolean
  stages?: Stage[]
  totalOpportunities?: number
  totalValue?: number
}

interface PipelineSelectorProps {
  pipelines: Pipeline[]
  selectedPipeline: Pipeline | null
  onSelect: (pipeline: Pipeline | null) => void
  onCreatePipeline: () => void
  subAccountId: string
}

export default function PipelineSelector({
  pipelines,
  selectedPipeline,
  onSelect,
  onCreatePipeline,
  subAccountId,
}: PipelineSelectorProps) {
  const [isOpen, setIsOpen] = useState(false)
  const [isCreating, setIsCreating] = useState(false)
  const [newPipelineName, setNewPipelineName] = useState('')
  const [creating, setCreating] = useState(false)
  
  const handleCreatePipeline = async () => {
    if (!newPipelineName.trim()) return
    
    try {
      setCreating(true)
      const response = await fetch('/api/v1/pipelines', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          subAccountId,
          name: newPipelineName.trim(),
          createDefaultStages: true,
        }),
      })
      
      const result = await response.json()
      if (!result.success) throw new Error(result.error)
      
      setNewPipelineName('')
      setIsCreating(false)
      setIsOpen(false)
      onCreatePipeline()
    } catch (err: any) {
      alert('Failed to create pipeline: ' + err.message)
    } finally {
      setCreating(false)
    }
  }
  
  return (
    <div className="relative">
      {/* Selected Pipeline Button */}
      <button
        onClick={() => setIsOpen(!isOpen)}
        className="flex items-center gap-2 px-4 py-2 bg-white border border-gray-200 rounded-lg hover:bg-gray-50 transition-colors min-w-[200px]"
      >
        {selectedPipeline ? (
          <>
            <span>{selectedPipeline.icon}</span>
            <span className="font-medium text-gray-900">{selectedPipeline.name}</span>
          </>
        ) : (
          <span className="text-gray-500">Select Pipeline</span>
        )}
        <span className="ml-auto text-gray-400">‚ñº</span>
      </button>
      
      {/* Dropdown */}
      {isOpen && (
        <>
          {/* Backdrop */}
          <div
            className="fixed inset-0 z-10"
            onClick={() => {
              setIsOpen(false)
              setIsCreating(false)
            }}
          />
          
          {/* Menu */}
          <div className="absolute top-full left-0 mt-1 w-72 bg-white border border-gray-200 rounded-lg shadow-lg z-20">
            {/* Pipeline List */}
            <div className="max-h-60 overflow-y-auto">
              {pipelines.map(pipeline => (
                <button
                  key={pipeline.id}
                  onClick={() => {
                    onSelect(pipeline)
                    setIsOpen(false)
                  }}
                  className={`
                    w-full flex items-center gap-3 px-4 py-3 text-left hover:bg-gray-50 transition-colors
                    ${selectedPipeline?.id === pipeline.id ? 'bg-green-50' : ''}
                  `}
                >
                  <span className="text-xl">{pipeline.icon}</span>
                  <div className="flex-1">
                    <p className="font-medium text-gray-900">{pipeline.name}</p>
                    {pipeline.isDefault && (
                      <p className="text-xs text-gray-400">Default</p>
                    )}
                  </div>
                  {selectedPipeline?.id === pipeline.id && (
                    <span className="text-green-600">‚úì</span>
                  )}
                </button>
              ))}
              
              {pipelines.length === 0 && !isCreating && (
                <div className="px-4 py-8 text-center text-gray-500">
                  <p>No pipelines yet</p>
                  <p className="text-sm">Create your first pipeline below</p>
                </div>
              )}
            </div>
            
            {/* Create New Pipeline */}
            <div className="border-t border-gray-200 p-3">
              {isCreating ? (
                <div className="space-y-2">
                  <input
                    type="text"
                    value={newPipelineName}
                    onChange={(e) => setNewPipelineName(e.target.value)}
                    placeholder="Pipeline name..."
                    className="w-full px-3 py-2 border border-gray-200 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent text-sm"
                    autoFocus
                    onKeyDown={(e) => {
                      if (e.key === 'Enter') handleCreatePipeline()
                      if (e.key === 'Escape') {
                        setIsCreating(false)
                        setNewPipelineName('')
                      }
                    }}
                  />
                  <div className="flex gap-2">
                    <button
                      onClick={handleCreatePipeline}
                      disabled={creating || !newPipelineName.trim()}
                      className="flex-1 px-3 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed text-sm"
                    >
                      {creating ? 'Creating...' : 'Create'}
                    </button>
                    <button
                      onClick={() => {
                        setIsCreating(false)
                        setNewPipelineName('')
                      }}
                      className="px-3 py-2 text-gray-600 hover:bg-gray-100 rounded-lg text-sm"
                    >
                      Cancel
                    </button>
                  </div>
                </div>
              ) : (
                <button
                  onClick={() => setIsCreating(true)}
                  className="w-full flex items-center justify-center gap-2 px-3 py-2 text-green-600 hover:bg-green-50 rounded-lg transition-colors text-sm"
                >
                  <span>+</span>
                  <span>Create New Pipeline</span>
                </button>
              )}
            </div>
          </div>
        </>
      )}
    </div>
  )
}
