'use client'

import { useState, useEffect, useRef } from 'react'

interface Contact {
  id: string
  firstName: string
  lastName: string
  email: string
  phone?: string
  company?: string
}

interface Stage {
  id: string
  name: string
  color: string
  stageType: string
  winProbability: number
  displayOrder?: number
  deals?: Opportunity[]
}

interface Pipeline {
  id: string
  name: string
  stages: Stage[]
}

interface Opportunity {
  id: string
  name: string
  description?: string
  value: number
  currency: string
  status: string
  priority: string
  probability: number
  expectedCloseDate?: string
  contact?: Contact
  contactId?: string
  stageId: string
  pipelineId: string
  tags: string[]
  source?: string
  lastActivityAt?: string
  createdAt?: string
}

interface OpportunityModalProps {
  isOpen: boolean
  onClose: () => void
  onSave: () => void
  opportunities?: Opportunity | null
  pipeline: Pipeline | null
  defaultStageId: string | null
  subAccountId: string
}

export default function OpportunityModal({
  isOpen,
  onClose,
  onSave,
  opportunity,
  pipeline,
  defaultStageId,
  subAccountId,
}: OpportunityModalProps) {
  const [formData, setFormData] = useState({
    description: '',
    value: 0,
    currency: 'USD',
    stageId: '',
    priority: 'medium',
    expectedCloseDate: '',
    source: '',
    tags: [] as string[],
    contactId: '',
  })
  const [saving, setSaving] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [contacts, setContacts] = useState<Contact[]>([])
  const [contactSearch, setContactSearch] = useState('')
  const [showContactDropdown, setShowContactDropdown] = useState(false)
  const [selectedContact, setSelectedContact] = useState<Contact | null>(null)
  const [tagInput, setTagInput] = useState('')
  const contactSearchRef = useRef<HTMLInputElement>(null)
  const dropdownRef = useRef<HTMLDivElement>(null)
  
  // Load form data when deal changes
  useEffect(() => {
    if (opportunity) {
      setFormData({
        description: opportunity.description || '',
        value: opportunity.value || 0,
        currency: opportunity.currency || 'USD',
        stageId: opportunity.stageId || '',
        priority: opportunity.priority || 'medium',
        expectedCloseDate: opportunity.expectedCloseDate?.split('T')[0] || '',
        source: opportunity.source || '',
        tags: opportunity.tags || [],
        contactId: opportunity.contactId || opportunity.contact?.id || '',
      })
      // Set selected contact from deal
      if (opportunity.contact) {
        setSelectedContact(opportunity.contact)
        setContactSearch(`${opportunity.contact.firstName} ${opportunity.contact.lastName}`)
      }
    } else {
      // Reset form for new deal
      setFormData({
        description: '',
        value: 0,
        currency: 'USD',
        stageId: defaultStageId || pipeline?.stages?.[0]?.id || '',
        priority: 'medium',
        expectedCloseDate: '',
        source: '',
        tags: [],
        contactId: '',
      })
      setSelectedContact(null)
      setContactSearch('')
    }
  }, [opportunity, defaultStageId, pipeline])
  
  // Fetch contacts for dropdown
  useEffect(() => {
    if (isOpen) {
      fetchContacts()
    }
  }, [isOpen])
  
  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
        setShowContactDropdown(false)
      }
    }
    document.addEventListener('mousedown', handleClickOutside)
    return () => document.removeEventListener('mousedown', handleClickOutside)
  }, [])
  
  const fetchContacts = async () => {
    try {
      const response = await fetch(`/api/v1/contacts?subAccountId=${subAccountId}&limit=100`)
      const result = await response.json()
      if (result.success && result.data) {
        setContacts(result.data.contacts || [])
      }
    } catch (err) {
      console.error('Failed to fetch contacts:', err)
      setContacts([])
    }
  }
  
  // Filter contacts based on search
  const filteredContacts = contacts.filter(c =>
    `${c.firstName} ${c.lastName} ${c.email} ${c.company || ''}`.toLowerCase().includes(contactSearch.toLowerCase())
  )
  
  const handleSelectContact = (contact: Contact) => {
    setSelectedContact(contact)
    setFormData({ ...formData, contactId: contact.id })
    setContactSearch(`${contact.firstName} ${contact.lastName}`)
    setShowContactDropdown(false)
  }
  
  const handleClearContact = () => {
    setSelectedContact(null)
    setFormData({ ...formData, contactId: '' })
    setContactSearch('')
    contactSearchRef.current?.focus()
  }
  
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setError(null)
    
    // Contact is required - opportunity title comes from contact name
    if (!selectedContact) {
      setError('Please select a contact for this opportunity')
      return
    }
    
    if (!formData.stageId) {
      setError('Please select a stage')
      return
    }
    
    try {
      setSaving(true)
      
      // Opportunity name is derived from contact's full name
      const opportunityName = `${selectedContact.firstName} ${selectedContact.lastName}`
      
      const payload = {
        ...formData,
        name: opportunityName, // Name synced with contact
        subAccountId,
        pipelineId: pipeline?.id,
        contactId: selectedContact.id,
      }
      
      const url = opportunity ? `/api/v1/deals/${opportunity.id}` : '/api/v1/deals'
      const method = opportunity ? 'PUT' : 'POST'
      
      const response = await fetch(url, {
        method,
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload),
      })
      
      const result = await response.json()
      
      if (!result.success) {
        throw new Error(result.error)
      }
      
      onSave()
    } catch (err: any) {
      setError(err.message || 'Failed to save opportunity')
    } finally {
      setSaving(false)
    }
  }
  
  const handleAddTag = () => {
    if (tagInput.trim() && !formData.tags.includes(tagInput.trim())) {
      setFormData({ ...formData, tags: [...formData.tags, tagInput.trim()] })
      setTagInput('')
    }
  }
  
  const handleRemoveTag = (tag: string) => {
    setFormData({ ...formData, tags: formData.tags.filter(t => t !== tag) })
  }
  
  if (!isOpen) return null
  
  return (
    <div className="fixed inset-0 z-50 overflow-y-auto">
      {/* Backdrop */}
      <div
        className="fixed inset-0 bg-black bg-opacity-50 transition-opacity"
        onClick={onClose}
      />
      
      {/* Modal */}
      <div className="flex min-h-full items-center justify-center p-4">
        <div className="relative bg-white rounded-xl shadow-xl max-w-2xl w-full max-h-[90vh] overflow-y-auto">
          {/* Header */}
          <div className="sticky top-0 bg-white border-b border-gray-200 px-6 py-4 flex items-center justify-between z-10">
            <h2 className="text-xl font-bold text-gray-900">
              {opportunity ? 'Edit Opportunity' : 'Create New Opportunity'}
            </h2>
            <button
              onClick={onClose}
              className="text-gray-400 hover:text-gray-600 transition-colors"
            >
              ‚úï
            </button>
          </div>
          
          {/* Form */}
          <form onSubmit={handleSubmit} className="p-6 space-y-6">
            {error && (
              <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded-lg">
                {error}
              </div>
            )}
            
            {/* Contact Search / Opportunity Title */}
            <div ref={dropdownRef} className="relative">
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Select Contact <span className="text-red-500">*</span>
                <span className="text-xs text-gray-500 font-normal ml-2">
                  (Contact name will be the Opportunity Title)
                </span>
              </label>
              
              {selectedContact ? (
                // Selected contact display
                <div className="flex items-center justify-between p-3 bg-green-50 border border-green-200 rounded-lg">
                  <div className="flex items-center gap-3">
                    <div className="w-10 h-10 rounded-full bg-green-600 text-white flex items-center justify-center font-semibold">
                      {selectedContact.firstName[0]}{selectedContact.lastName[0]}
                    </div>
                    <div>
                      <div className="font-medium text-gray-900">
                        {selectedContact.firstName} {selectedContact.lastName}
                      </div>
                      <div className="text-sm text-gray-500">
                        {selectedContact.email}
                        {selectedContact.company && ` ‚Ä¢ ${selectedContact.company}`}
                      </div>
                    </div>
                  </div>
                  <button
                    type="button"
                    onClick={handleClearContact}
                    className="text-gray-400 hover:text-gray-600 p-1"
                    title="Change contact"
                  >
                    <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                </div>
              ) : (
                // Contact search input
                <>
                  <div className="relative">
                    <svg className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                    </svg>
                    <input
                      ref={contactSearchRef}
                      type="text"
                      value={contactSearch}
                      onChange={(e) => {
                        setContactSearch(e.target.value)
                        setShowContactDropdown(true)
                      }}
                      onFocus={() => setShowContactDropdown(true)}
                      className="w-full pl-10 pr-4 py-2 border border-gray-200 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                      placeholder="Search contacts by name, email, or company..."
                    />
                  </div>
                  
                  {/* Contact Dropdown */}
                  {showContactDropdown && (
                    <div className="absolute z-20 w-full mt-1 bg-white border border-gray-200 rounded-lg shadow-lg max-h-60 overflow-y-auto">
                      {filteredContacts.length === 0 ? (
                        <div className="px-4 py-3 text-gray-500 text-center">
                          {contactSearch ? 'No contacts found' : 'Start typing to search contacts'}
                        </div>
                      ) : (
                        filteredContacts.map(contact => (
                          <button
                            key={contact.id}
                            type="button"
                            onClick={() => handleSelectContact(contact)}
                            className="w-full px-4 py-3 text-left hover:bg-gray-50 flex items-center gap-3 border-b border-gray-100 last:border-0"
                          >
                            <div className="w-8 h-8 rounded-full bg-gray-200 text-gray-600 flex items-center justify-center text-sm font-medium">
                              {contact.firstName[0]}{contact.lastName[0]}
                            </div>
                            <div className="flex-1 min-w-0">
                              <div className="font-medium text-gray-900 truncate">
                                {contact.firstName} {contact.lastName}
                              </div>
                              <div className="text-sm text-gray-500 truncate">
                                {contact.email}
                                {contact.company && ` ‚Ä¢ ${contact.company}`}
                              </div>
                            </div>
                          </button>
                        ))
                      )}
                    </div>
                  )}
                </>
              )}
              
              {/* Info message */}
              <p className="mt-2 text-xs text-gray-500">
                <svg className="inline w-4 h-4 mr-1 text-blue-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
                The opportunity title stays synchronized with the contact's name. If you update the contact's name later, this opportunity title will also update.
              </p>
            </div>
            
            {/* Value and Stage */}
            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Opportunity Value
                </label>
                <div className="flex">
                  <select
                    value={formData.currency}
                    onChange={(e) => setFormData({ ...formData, currency: e.target.value })}
                    className="px-3 py-2 border border-r-0 border-gray-200 rounded-l-lg bg-gray-50 text-gray-600"
                  >
                    <option value="USD">$</option>
                    <option value="EUR">‚Ç¨</option>
                    <option value="GBP">¬£</option>
                    <option value="CAD">C$</option>
                  </select>
                  <input
                    type="number"
                    value={formData.value}
                    onChange={(e) => setFormData({ ...formData, value: parseFloat(e.target.value) || 0 })}
                    className="flex-1 px-4 py-2 border border-gray-200 rounded-r-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                    min="0"
                    step="0.01"
                  />
                </div>
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Stage <span className="text-red-500">*</span>
                </label>
                <select
                  value={formData.stageId}
                  onChange={(e) => setFormData({ ...formData, stageId: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                  required
                >
                  <option value="">Select stage...</option>
                  {pipeline?.stages.map(stage => (
                    <option key={stage.id} value={stage.id}>
                      {stage.name} ({stage.winProbability}%)
                    </option>
                  ))}
                </select>
              </div>
            </div>
            
            {/* Priority and Expected Close Date */}
            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Priority
                </label>
                <select
                  value={formData.priority}
                  onChange={(e) => setFormData({ ...formData, priority: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                >
                  <option value="low">Low</option>
                  <option value="medium">Medium</option>
                  <option value="high">High</option>
                  <option value="urgent">Urgent</option>
                </select>
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Expected Close Date
                </label>
                <input
                  type="date"
                  value={formData.expectedCloseDate}
                  onChange={(e) => setFormData({ ...formData, expectedCloseDate: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                />
              </div>
            </div>
            
            {/* Source */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Lead Source
              </label>
              <select
                value={formData.source}
                onChange={(e) => setFormData({ ...formData, source: e.target.value })}
                className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
              >
                <option value="">Select source...</option>
                <option value="website">Website</option>
                <option value="referral">Referral</option>
                <option value="cold_call">Cold Call</option>
                <option value="advertisement">Advertisement</option>
                <option value="social_media">Social Media</option>
                <option value="email_campaign">Email Campaign</option>
                <option value="trade_show">Trade Show</option>
                <option value="other">Other</option>
              </select>
            </div>
            
            {/* Tags */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Tags
              </label>
              <div className="flex flex-wrap gap-2 mb-2">
                {formData.tags.map(tag => (
                  <span
                    key={tag}
                    className="inline-flex items-center gap-1 px-2 py-1 bg-green-100 text-green-700 rounded-full text-sm"
                  >
                    {tag}
                    <button
                      type="button"
                      onClick={() => handleRemoveTag(tag)}
                      className="hover:text-green-900"
                    >
                      √ó
                    </button>
                  </span>
                ))}
              </div>
              <div className="flex gap-2">
                <input
                  type="text"
                  value={tagInput}
                  onChange={(e) => setTagInput(e.target.value)}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter') {
                      e.preventDefault()
                      handleAddTag()
                    }
                  }}
                  className="flex-1 px-4 py-2 border border-gray-200 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                  placeholder="Add a tag..."
                />
                <button
                  type="button"
                  onClick={handleAddTag}
                  className="px-4 py-2 bg-gray-100 text-gray-700 rounded-lg hover:bg-gray-200"
                >
                  Add
                </button>
              </div>
            </div>
            
            {/* Description */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Notes
              </label>
              <textarea
                value={formData.description}
                onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                rows={3}
                className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
                placeholder="Add notes about this opportunity..."
              />
            </div>
            
            {/* Actions */}
            <div className="flex items-center justify-end gap-3 pt-4 border-t border-gray-200">
              <button
                type="button"
                onClick={onClose}
                className="px-4 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
              >
                Cancel
              </button>
              <button
                type="submit"
                disabled={saving || !selectedContact}
                className="px-6 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
              >
                {saving ? 'Saving...' : opportunity ? 'Update Opportunity' : 'Create Opportunity'}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}
